.machine {
  height: 100vh;
  background: url(../assets/imgs/machine/bg.png);
  /* 标题开始 */
  /* 标题结束 */
  /* 服务器列表开始 */
  /* 服务器列表结束 */
}
.machine .title {
  position: absolute;
  top: 0;
  width: 100%;
  height: 80px;
  text-align: center;
  color: #f6f6f8;
  background: url(../assets/imgs/index/head.png) no-repeat;
  background-size: 100%;
  background-position: bottom center;
  z-index: 10;
}
.machine .title .txt-center h1 {
  font-size: 35px;
  font-weight: 400;
}
.machine .title .txt-center p {
  font-size: 14px;
  font-weight: 400;
}
.machine .picimg {
  position: absolute;
  top: 170px;
  left: 500px;
}
.machine .list {
  position: absolute;
  top: 350px;
  right: 500px;
  color: #fff;
}
.machine .list .list-title {
  height: 20px;
  margin-bottom: 30px;
}
.machine .list .list-title p {
  display: inline-block;
  height: 20px;
  line-height: 20px;
  text-indent: 10px;
  vertical-align: top;
}
.machine .list .list-title img {
  margin-left: 50px;
}
.machine .list .imglist .img-item img {
  height: 50px;
}
.machine .list .imglist .img-item .img-txt {
  display: inline-block;
  vertical-align: top;
}
.machine .list .imglist .img-item .img-txt {
  width: 184px;
  height: 44px;
  line-height: 44px;
  background: url(../assets/imgs/machine/yiun.png) no-repeat;
  background-size: 100% 100%;
  text-indent: 10px;
  color: #12FBFB;
}
.machine .list .back {
  height: 15px;
  font-size: 13px;
  margin-top: 150px;
  color: #4FDDF8;
  line-height: 15px;
}
.machine .list .back span {
  vertical-align: top;
  margin: 0 10px;
}
.machine .list .back img:nth-child(1) {
  transform: rotate(-180deg);
}
.machine .list .back:hover {
  cursor: pointer;
}
/* 全网信息统计开始 */
.left-top {
  width: 400px;
  position: absolute;
  top: 50px;
  left: 30px;
}
.left-top .left-top-title {
  width: 160px;
  height: 45px;
  line-height: 45px;
  text-indent: 20px;
  color: #fff;
  font-size: 18px;
  background: url(../assets/imgs/index/daohang.png);
  background-size: 100% 100%;
}
.left-top .back {
  box-sizing: border-box;
  padding-top: 40px;
  background: url('../assets/imgs/index/zhanshi.png') no-repeat;
  background-size: 100% 100%;
  margin-top: 5px;
}
.left-top .linear-top {
  color: #fff;
  box-sizing: border-box;
  padding-left: 50px;
  /* 设备统计标题开始 */
  /* 设备统计标题结束 */
  /* 设备统计分割线开始 */
  /* 设备统计分割线结束 */
}
.left-top .linear-top .top-title .item {
  display: inline-block;
  margin-right: 50px;
}
.left-top .linear-top .top-title .item p {
  text-align: center;
}
.left-top .linear-top .top-title .item p:nth-child(1) {
  font-weight: bold;
  color: #12FBFB;
  font-size: 23px;
}
.left-top .linear-top .top-title .item p:nth-child(2) {
  color: #8B93AF;
  font-size: 14px;
}
.left-top .linear-top .top-title .item:nth-child(2) p:nth-child(1) {
  color: #3D78DC;
}
.left-top .linear-top .top-title .item:nth-child(3) p:nth-child(1) {
  color: #FE717B;
}
.left-top .linear-top .linear-type {
  margin-top: 25px;
}
.left-top .linear-top .linear-title {
  position: relative;
  top: 20px;
}
.left-top .linear-top .linear-title span {
  display: inline-block;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: #76fcff;
  margin-right: 5px;
}
.left-top .pic ul {
  z-index: 999;
  display: inline-block;
  color: #fff;
  position: relative;
  top: -30px;
  left: 20px;
  margin-right: 50px;
}
.left-top .pic ul li {
  height: 40px;
  line-height: 40px;
  font-size: 14px;
}
.left-top .pic ul li > span {
  display: inline-block;
  width: 10px;
  height: 10px;
  border-radius: 50px;
  background: #0D80CB;
}
.left-top .pic ul li p {
  display: inline-block;
  margin-left: 15px;
}
.left-top .pic ul li p span {
  margin: 0 5px;
}
.left-top .pic ul li:nth-child(2) > span {
  background: #46B7B5;
}
.left-top .pic ul li:nth-child(3) > span {
  background: #01EAF9;
}
.left-top .pic ul li:nth-child(4) > span {
  background: #8492E5;
}
/* 全网信息统计 */
/* 最新预警开始 */
.new {
  width: 400px;
  height: 606px;
  background: url(../assets/imgs/index/zhanshi.png) no-repeat;
  background-size: 100% 100%;
  position: absolute;
  right: 30px;
  top: 100px;
}
.new .new-title {
  text-align: left;
  padding: 20px;
  height: 18px;
  font-size: 18px;
  font-family: Microsoft YaHei;
  font-weight: 400;
  color: #FFFFFF;
}
.new .cpu {
  width: 300px;
  height: 30px;
  line-height: 30px;
  background: #1E2282;
  color: #12FBFB;
  border: 1px solid #2741c6;
  font-size: 14px;
  margin: 10px 0 0 5px;
}
.new .cpu img {
  margin: 0 10px;
}
.new .alert {
  box-sizing: border-box;
  padding-left: 55px;
  margin: 30px 0;
}
.new .alert li {
  display: inline-block;
  width: 40px;
  text-align: center;
  color: #fff;
  font-size: 14px;
  margin-right: 75px;
}
.new .alert li img {
  width: 35px;
}
/* 最新预警结束 */
