


























































































































































































































.machine{
  height: 100vh;
  background: url(../assets/imgs/machine/bg.png);
  /* 标题开始 */
      .title{
        position: absolute;
        top: 0;
        width: 100%;
        height: 80px;
        text-align: center;
        color: #f6f6f8;
        background: url(../assets/imgs/index/head.png) no-repeat;
        background-size: 100%;
        background-position: bottom center;
        z-index: 10;
        .txt-center{
              h1{
                font-size: 35px;
                font-weight: 400;
              }
              p{
                font-size: 14px;
                font-weight: 400;
              }
            }
      }
      /* 标题结束 */
  .picimg{
    position: absolute;
    top: 170px;
    left: 500px;
  }

  /* 服务器列表开始 */
  .list{
    position: absolute;
    top: 350px;
    right: 500px;
    color: #fff;
    .list-title{
      height: 20px;
      margin-bottom: 30px;
      p{
        display: inline-block;
        height: 20px;
        line-height: 20px;
        text-indent: 10px;
        vertical-align: top;
      }
      img{
        margin-left: 50px;
      }
    }

    .imglist{
      .img-item{
        img{
          height: 50px;
        }
        .img-txt{
          display: inline-block;
          vertical-align:top,
        }
        .img-txt{
          width: 184px;
          height: 44px;
          line-height: 44px;
          background: url(../assets/imgs/machine/yiun.png) no-repeat;
           background-size: 100% 100%;
           text-indent: 10px;
           color: #12FBFB;
        }
      }
    }


    .back{
      height: 15px;
      font-size: 13px;
      margin-top: 150px;
      color: #4FDDF8;
      line-height: 15px;
      span{
        vertical-align: top;
        margin: 0 10px;
      }
      img:nth-child(1){
        transform: rotate(-180deg);
      }
    }
    .back:hover{
      cursor: pointer;
    }
  }
  /* 服务器列表结束 */

}
/* 全网信息统计开始 */
.left-top{
  width: 400px;
  position: absolute;
  top: 50px;
  left: 30px;
  .left-top-title{
    width: 160px;
    height: 45px;
    line-height: 45px;
    text-indent: 20px;
    color: #fff;
    font-size: 18px;
    background: url(../assets/imgs/index/daohang.png);
    background-size: 100% 100%;
  }
  .back{
    box-sizing: border-box;
    padding-top: 40px;
    background: url('../assets/imgs/index/zhanshi.png') no-repeat;
    background-size: 100% 100%;
    margin-top: 5px;
  }
  .linear-top{
    color: #fff;
    box-sizing: border-box;
    padding-left: 50px;
    /* 设备统计标题开始 */
    .top-title{
      .item{
        display: inline-block;
        margin-right: 50px;
        p{
          text-align: center;
        }
        p:nth-child(1){
          font-weight: bold;
          color: #12FBFB;
          font-size: 23px;
        }
        p:nth-child(2){
          color: #8B93AF;
          font-size: 14px;
        }
      }
      .item:nth-child(2) p:nth-child(1){
        color: #3D78DC;
      }
      .item:nth-child(3) p:nth-child(1){
        color: #FE717B;
      }
    }
    /* 设备统计标题结束 */
    /* 设备统计分割线开始 */
    .linear-type{margin-top: 25px;}
    .linear-title{
      position: relative;
      top: 20px;
      span{
        display: inline-block;
        width: 10px;
        height: 10px;
        border-radius: 50%;
        background: #76fcff;
        margin-right: 5px;
      }
    }
    /* 设备统计分割线结束 */

  }
  .pic{
    ul{
      z-index: 999;
      display: inline-block;
      color: #fff;
      position: relative;
      top: -30px;
      left: 20px;
      margin-right: 50px;
      li{
        height: 40px;
        line-height: 40px;
        font-size: 14px;
        >span{
          display: inline-block;
          width: 10px;
          height: 10px;
          border-radius: 50px;
          background: #0D80CB;
        }
        p{
          display: inline-block;
          margin-left: 15px;
          span{
            margin: 0 5px;
          }
        }
      }
      li:nth-child(2)>span{
        background: #46B7B5;
      }
      li:nth-child(3)>span{
        background: #01EAF9;
      }
      li:nth-child(4)>span{
        background: #8492E5;
      }
    }
  }
}
/* 全网信息统计 */

/* 最新预警开始 */
.new{
  width: 400px;
  height: 606px;
  background: url(../assets/imgs/index/zhanshi.png) no-repeat;
  background-size: 100% 100%;
  position: absolute;
  right: 30px;
  top: 100px;
  .new-title{
    text-align: left;
    padding: 20px;
    height: 18px;
    font-size: 18px;
    font-family: Microsoft YaHei;
    font-weight: 400;
    color: #FFFFFF;
  }
  .cpu{
    width: 300px;
    height: 30px;
    line-height: 30px;
    background: #1E2282;
    color: #12FBFB;
    border: 1px solid #2741c6;
    font-size: 14px;
    margin: 10px 0 0 5px;
    img{
      margin: 0 10px;
    }
  }

  .alert{
    box-sizing: border-box;
    padding-left: 55px;
    margin: 30px 0;
    li{
      display: inline-block;
      width: 40px;
      text-align: center;
      color: #fff;
      font-size: 14px;
      margin-right: 75px;
      img{
        width: 35px;
      }
    }
  }

}
/* 最新预警结束 */

