.pic {
  width: 100%;
  height: 180px;
  margin-top: 30px;
}
.pic1 {
  display: inline-block;
  width: 200px;
  position: relative;
  z-index: 999;
}
.pic1 .txt {
  color: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -5px;
  margin-left: -30px;
  font-size: 14px;
}
.pic1 .txt p {
  text-align: center;
}
